.header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  color: #004d40;
  background-color: rgba(236,239,241,0.75);
  z-index: 1000;
  border-bottom: 2px rgba(0, 105, 92, 0.85) solid;
}
.header nav {
  background-color: transparent;
  box-shadow: none;
  -webkit-box-shadow: none;
  /* border-bottom: 1px #00695c solid; */
}

.header .author-name {
  font-size: 1.2rem;
  font-weight: 100;
}
.header ul:not(.sidenav) li:not(.home-nav-li) {
  width: calc(100vw / 10);
  text-align: center;
}

.header ul:not(.sidenav) li a {
  position: relative;
  text-decoration: none;
  width: 100%;
  color: #4e342e;
  color: #004d40;
  font-size: 1.35rem;
  font-weight: 400;
  margin: 0px 10px;
  margin-left: auto;
  margin-right: auto;
  text-transform: uppercase;
  font-family: Roboto,Oxygen-Sans,-apple-system,BlinkMacSystemFont,"Segoe UI",Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
  -webkit-transition: opacity 1s, font-size 0.5s ease-in-out;
    -o-transition: opacity 1s, font-size 0.5s ease-in-out;
    -webkit-transition:visibility 0.3s linear,opacity 0.3s linear;
    -o-transition:visibility 0.3s linear,opacity 0.3s linear;
    transition:visibility 0.3s linear,opacity 0.3s linear;
}
.header ul:not(.sidenav) li a:hover {
  background-color: transparent;
}

.header ul:not(.sidenav) li a:not(.nav-home) span:before {
  position: absolute;
  left: 25%;
  bottom: 20px;
  height: 2px;
  width: 50%;
  background-color: rgba(122,77,0,0.6);
  content: "";
  opacity: 0.0;
  visibility: hidden;
  -webkit-transition: opacity 0.35s ease-in-out;
  -o-transition: opacity 0.35s ease-in-out;
  transition: opacity 0.35s ease-in-out;
}

.header ul:not(.sidenav) li a:not(.nav-home):hover span:before {
  visibility: visible;
  opacity: 1;
}

.header .navbar-links {
  color: #fff;
  display: inline-block;
  font-size: 2.1rem;
  padding: 0;
}

.header nav ul li a.nav-home {
  font-size: 2.25rem;
  font-weight: 100;
  width: 225px;
  text-transform: initial;
}
.header a.nav-home img {
  height: 110px;
  border: 0.5px rgba(122,77,0,0.9) solid;
  margin-top: 5px;
}
.header a.nav-home:after {
  content: '1';
  height: 120px;
  width: 120px;
  margin-left: auto;
  margin-right: auto;
  background-color: #004d40;
  opacity: 0;
  border-radius: 50%;
  position: absolute;
  left: 52.5px;
  z-index: -1;
  top: 0;

}

.header .cd-top {
  text-indent: initial;
  cursor: pointer;
}

